.menu_div{
    width: 1100px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-family: 'Noto Sans KR', sans-serif;
    font-size: 14px;
}

.menu_div > span{
    width: 200px;
    height: inherit;
    color:#e82115;
    font-size: 15px;
    font-weight: 500;
    line-height: 50px;
    text-align: center;
    cursor: pointer;
}

.menu_list_ul{
    width: 630px;
    height: inherit;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-weight: 400;
}

.menu_list_ul > li:hover{
    cursor: pointer;
    color: #e82115;
    font-weight: 500;
}

.menu_user_ul{
    width: 270px;
    height: inherit;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    color:#222222;
    font-weight: 400;
}

.menu_user_ul > li{
    display: flex;
}

.menu_user_ul > li:hover{
    cursor: pointer;
    color: #e82115;
    font-weight: 500;
}

.menu_user_ul > li > a {
    margin-left: 7px;
}
