.copy_full{
    width: 100%;
    height: 300px;
    border-top: 3px solid black;
    border-bottom: 1px solid black;
}

.copy_full_title{
    width: 100%;
    height: 50px;
    border-bottom: 1px solid black;
}

.copy_inner_title{
    margin: 0 auto;
    width: 1100px;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.copy_inner_title > ul:nth-of-type(1){
    display: flex;
    align-items: center;
        justify-content: space-around;
    width: 700px;
    height: 50px;
    font-size: 15px;
    color: #333333;
    font-weight: 500;
}

.copy_inner_title > ul:nth-of-type(2){
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 400px;
    height: 50px;
    font-size: 15px;
    color: #333333;
    font-weight: 500;
}

.copy_inner_title > ul:nth-of-type(2) > li > img{
    height: 15px;
}

.copy_inner_title > ul:nth-of-type(1) > li{
    height: 20px;
    padding-left: 30px;
    border-left: 1px solid black;
}

.copy_inner_title > ul:nth-of-type(1) > li:first-child{
    border-left: none;
}

.copy_inner_index{
    display: flex;
    margin: 0 auto;
    width: 1100px;
    height: 240px;
}

.copy_inner_index > div:nth-of-type(1){
    display: flex;
    align-items: center;
    height: 160px;
    width: 700px;
    height: inherit;
}

.copy_inner_index > div > ul{
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    padding-left: 25px;
    border-left: 1px solid black;
    height: 160px;
}

.copy_inner_index > div:nth-of-type(2){
    display: flex;
    align-items: center;
    height: 160px;
    width: 400px;
    height: inherit;
}

.imgbank{
    height: 13px;
}

.copy_table{
    height: 160px;
}


.cupoint{
    cursor: pointer;
}