.top_ulcss{
    width: 100%;
    height: inherit;
    font-family: 'Noto Sans KR', sans-serif;
    position: relative;
}

.top_ulcss_li1{
    width: 100%;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #E82115;
    background-image: url('../public/mall_img/ad/top_ad.png');
    background-repeat: no-repeat;
    background-position: center;
    position: relative;
}

.top_ulcss > li:nth-of-type(1) > input{
    width: 25px;
    height: 25px;
    background-color: #000000;
    color: #ffffff;
    margin-left:1050px;
}

.top_ulcss > li:nth-of-type(2){
    margin:0 auto;
    width: 100%;
    height: 40px;
    background-color: #747474;
    font-size: 14px;
}


.top_ul_li_list{
    margin: 0 auto;
    width: 1100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.top_ul_li_list > div{
    width: 380px;
    height: inherit;
    line-height: 40px;
}

.top_ul_userselect{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 14px;
}

.top_ul_userselect > li{
    cursor: pointer;
    height: 15px;
    line-height: 13px;
    padding-left:10px;
    padding-right:10px;
    border-left: 1px solid black;
}

.top_ul_userselect > :first-of-type{
    border-left: 0px solid black;
}

.top_ulcss > li:nth-of-type(3){
    width: 100%;
    height: 110px;
    background-color: #ffffff;
}

.top_ulcss > li:nth-of-type(3) > ul{
    margin:0 auto;
    width: 1100px;
    height: 110px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.top_ulcss > li:nth-of-type(3) > ul > li:nth-of-type(1){
    width: 300px;
    height: 110px;
    line-height: 110px;
    display: flex;
    align-items: center;
}

.top_ulcss > li:nth-of-type(3) > ul > li:nth-of-type(1) > img{
    width: 200px;
    height: 50px;
}
.top_ulcss > li:nth-of-type(3) > ul > li:nth-of-type(2){
    width: 340px;
    height: 40px;
}
.serach_div {
    width: 340px;
    height: 40px;
    border-bottom: 2px solid #ed4747;
    display: flex;
    justify-content: space-between;
    align-items: center;
    outline : none;
}
.serach_input {
    width: 270px;
    height: 40px;
    border: none;
    background: none;
}

.serach_input:focus{
    outline: none;
}

.coupon_size{
    width: 265px;
    height: 55px;
}

.serach_input_btn{
width: 30px;
height: 30px;
background-image: url('../public/topimage/top_btn_search.gif');
}